import React, { useEffect, useState } from "react";
import { Button, Card, Collapse, Col, Row } from "antd";
import * as Realm from "realm-web";

import {
  CaretRightOutlined,
  LinkedinFilled,
  GithubOutlined,
} from "@ant-design/icons";

import { DeviceSizeView, Separator, VideoSlide } from "components/common";

import { GlitchyText } from "components/effects";

import logo from "assets/logo.png";

const { Panel } = Collapse;
const { Meta } = Card;

const gutter = [
  { xs: 8, sm: 16, md: 32 },
  { xs: 8, sm: 16, md: 32 },
];

const background = {
  background:
    'linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/img/backgrounds/thumbnail.png")',
  backgroundSize: "cover",
  backgroundPosition: "center",
};

const button = {
  fontSize: "1.8rem",
  border: "none",
  width: "10rem",
};

function calculateAge(birthday) {
  // birthday is a date
  var ageDifMs = Date.now() - birthday.getTime();
  var ageDate = new Date(ageDifMs); // miliseconds from epoch
  return Math.abs(ageDate.getUTCFullYear() - 1970);
}

// Components
const SkillCard = ({ title, description, icon }) => {
  return (
    <>
      <Col sm={12} md={8} style={{ width: "100%" }}>
        <Card
          hoverable
          style={{ minHeight: "100%", minWidth: "100%", paddingTop: 24 }}
          cover={
            <img
              alt={title}
              src={icon}
              style={{ maxWidth: 60, margin: "auto", borderRadius: 0 }}
            />
          }
        >
          <Meta title={title} description={description} />
        </Card>
      </Col>
    </>
  );
};

// Sections

const AboutSection = () => {
  return (
    <>
        <h2>What is MIDGARD?</h2>
      <Separator height={2} />

      <h5 className="description">
        MIDGARD is an open-source simulatior for autonomous robot navigation in
        outdoor unstructured environments. 
        <br /> 
        It is designed to
        enable the training of autonomous agents (e.g., unmanned ground
        vehicles) in photorealistic 3D environments, and support the
        generalization skills of learning-based agents thanks to the variability
        in training scenarios.
      </h5>
    </>
  );
};

const SkillSection = () => {
  return (
    <div>
      <h2>Why MIDGARD?</h2>
      <Separator height={1} />

      <Row gutter={gutter} justify="center">
        <SkillCard
          title="Deep Learning"
          description="Designed to support Deep Reinforcement Learning applications."
          icon="/img/icons/deep.svg"
        />

        <SkillCard
          title="Navigation"
          description="Designed to tackle the task of autonomus navigation in unstructured environments."
          icon="/img/icons/nav.svg"
        />

        <SkillCard
          title="Procedural generation"
          description="Infinite variations of each navigation scene."
          icon="/img/icons/gear.svg"
        />

        <SkillCard
          title="Unreal Engine based"
          description="The simulator is developed in unreal to provide photorealism."
          icon="/img/icons/business/unreal-engine.svg"
        />
      </Row>
    </div>
  );
};

// Home Body
const Home = () => {
  const [news, setNews] = useState([]);

  return (
    <>
      <div className="slide slide-first white-text" style={background}>
        <div className="container centered">
          <img
            src={logo}
            alt="mascotte"
            style={{ maxWidth: "200px", margin: "20px" }}
          />
          <h1>MIDGARD</h1>
        </div>
      </div>

      <div className="slide dotted">
        <div className="container centered">
          <div>
            <AboutSection />
            <Separator height={6} />
            <SkillSection />
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
